@import 'styles/Colors';
.background-skewer{
    display: flex;
    flex-direction: row;
    align-items: center;
    position: relative;
    width: 100%;
    z-index: 1;
    &__circle {
        width: 0.625rem;
        height: 0.625rem;
        border-radius: 50%;
        border: 1px solid white;
    } 
    &__line {
        flex-grow: 1;
        height: 0.0625rem;
        background-color: white; 
    }

}
.last-skewer::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 50%; 
    background-color: $footerBackgroundColor; 
    z-index: -1; 
    transform-origin: bottom left;
}

.recommendation {
    padding: 3.75rem 7rem;
    width: 100%; 
    display: flex;
    flex-direction: column;
    @media screen and (max-width: 1024px) {
        padding: 3.75rem 4.5rem 7rem 4.5rem;
    }
    @media screen and (max-width: 768px) {
        padding: 1.875rem  2rem;
    }
    h1{
        font-size: 2em;
        font-family: 'Century Gothic';
        color: $highlightedItemColor;
        font-weight: bold;
        margin-top: 0;
        margin-bottom: 2rem;
        letter-spacing: 0.15em;
        @media screen and (max-width: 1024px) {
            font-size: 2em;
            margin-bottom: 2rem;
        }
        @media screen and (max-width: 540px) {
            font-size: 1.375em;
            margin-bottom: 1.375rem;
        }
    }
    &__product-card {
        color: $defaultTextColor;
        font-family: 'Inter';
        margin: 0rem 1rem; 
        @media screen and (max-width: 768px) {
            margin: 0rem 0.5rem;
        }
    }
    &__product-card-image-container {
        height: 12.5rem;
        overflow: hidden; 
        display: flex;
        align-items: center;
        & a:hover{
            opacity: 0.7;
            transition: 0.5s;
        }
        img {
            max-width: 100%; 
            height: auto;
        }
    }
    &__product-info{
        display: flex;
        flex-direction: column;
        margin-bottom: 1rem;
        font-weight: 400;
        a{
            height: 3rem;
            text-decoration: none;
            font-size: 1.25em;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
            overflow: hidden;
            text-overflow: ellipsis; 
            color: $defaultTextColor;
        }
        & a:hover{
            color: $highlightedItemColor;
        }
        span{
            font-size: 1em;
        }
    }
    &__buy-button{
        width: fit-content;
        padding: 0.3125rem 1.25rem;
        border: 1px solid $borderColor;
        &:hover {
            background-color: $highlightedItemColor; 
            cursor: pointer;
            transition: 0.5s;
        }        
    }

    .slick-arrow{
        width: 60px;
        height: 60px; 
        fill: $borderColor;
        @media screen and (max-width: 768px) {
            width: 2rem;
            height: 2rem; 
        }
    }
    .slick-arrow.slick-disabled {
        fill: $arrowControllerColor; 
    }
    
    .slick-next{
        right: -85px;
        @media screen and (max-width: 1024px) {
            right: -60px;
        }
        @media screen and (max-width: 768px) {
            right: -2rem;
        }
        @media screen and (max-width: 600px) {
            display: none!important;
        }
    }
    .slick-prev{
        left: -85px;
        @media screen and (max-width: 1024px) {
            left: -60px;
        }
        @media screen and (max-width: 768px) {
            left: -2rem;
        }
        @media screen and (max-width: 600px) {
            display: none!important;
        }
    }
    
    .slick-slider{
        &:hover {
            cursor: grab; 
        }
    }


    &__custom-arrows{
        margin-top: 1.875rem;
        display: flex;
        flex-direction: row;
        align-items: center;
        @media screen and (min-width: 600px) {
            display: none;
        }
        button {
            display: flex;
            width: 3rem;
            height: 3rem;
            align-items: center;
            justify-content: center;
            border: none;
            outline: none;
            background-color: $priceButtonBackground;
            color: $previousButtonColorRecommendations;    
            transition: all .5s;
            font-weight: 400;
            font-family: 'Quicksand';
            font-size: 1.5rem;
            &:disabled {
                color: black;
                cursor: not-allowed; 
            }
        }
        span {
            font-family: 'Quicksand';
            font-size: 1.125em;
            letter-spacing: 0.1em;
            font-weight: 400;
            color: $defaultTextColor;
        }
      }
    
    &__custom-arrows button:hover {
      color: $highlightedItemColor;
      cursor: pointer;
    }
    
}